body {
  background-color: #121212;
}
.container .title {
  color: #fff;
  font-size: 2.5rem;
  text-align: center;
  margin: 2rem 0 1rem;
}

.title .query-text {
  color: #f7d354;
}

.movies-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.movies-container div {
  width: 30%;
  color: #fff;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #111;
}

.movies-container div img{
    max-width: 100%;
}

.movies-container div img,
.movies-container div h2,
.movies-container div p{
    margin-bottom: 1rem;
}

.movies-container div a{
    background-color: #f7d354;
    border: 2px solid #f7d354;
    color: #000;
    padding: 1rem 0.5rem;
    align-items: center;
    cursor: pointer;
    transition: .4s;
    text-align: center;
    font-weight: bold;
    text-decoration: none;
}

.movies-container div a:hover{
    background-color: transparent;
    color: #f7d354;
}