#navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem ;
    background-color: #121212;
}

#navbar h2 a {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: #f7d354;
    text-decoration: none;
    text-transform: uppercase;
}

#navbar form{
    display: flex;
    gap: .5rem;
}

#navbar input{
    padding: 0.2rem 0.8rem;
    border-radius:  4px;
    border: none;
}

#navbar form button{
    background-color: #f7d354;
    border: 2px solid #f7d354;
    color: #000;
    padding: .3rem;
    font-size: 1.3rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: .4s;
}

#navbar form button:hover{
    background-color: transparent;
    color: #f7d354;
}